.contact{
    background: url('http://34.65.52.225:3000/uploads/sintal-images/2836852.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 100px 0;
    transition: all 0.3s ease;
}
.contact .form{
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    padding: 35px 80px;
    background: rgb(255, 255, 255);
    border-radius: 15px;
    min-height: 300px;
}

.contact  input, textarea{
    height: 30px;
    margin: 5px 0;
}

.contact textarea{
    height: 100px;
    resize: none;
    padding: 10px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
}

.contact button{
    margin-top: 10px;
    padding: 13px 30px;
    background-color: black;
    color: white;
    border: none;
    font-size: 1.05rem;
}

.contact .form h1{
    font-size: 2.5rem;
    padding-bottom: 10px;
    text-align: center;
}
.contact .contant-info{
    padding-bottom: 25px;
}

.contact .form .form-info{
    padding: 25px 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.form-inputs{
    display: flex;
    flex-direction: column;
}

.submitted-form{
    padding: 70px 0 50px;
    text-align: center;
    font-size: 1.6rem;
    font-weight: bold;
}