.navbar{
    width: 100%;
    max-width: 750px;
}

.navbar ul {
    list-style-type: none;
    display: flex;
    justify-content: space-around;
}

.navbar li {
    display: inline;
}



.navbar li a {
    text-transform: uppercase;
    color: white;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    border-radius: 8px;
    padding: 10px 18px;
    transition: background-color 0.2s ease-in;
}

.navbar a:hover{
    color: black;
    background-color: rgb(255, 255, 255);
}

.navbar .active {
    color: black;
    background-color: rgb(255, 255, 255);
}