.gallery-header{
    background: url('http://34.65.52.225:3000/uploads/sintal-images/h1-parallax-1.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    padding: 120px 0;
    text-align: center;
    color: white;
}

.gallery-header h1{
    padding: 30px 0 5px;
    font-size: 4rem;
}
.gallery-header p{
    font-size: 1.1rem;
}
.thumbs{
    width: 70%;
    margin: auto;
}

.thumbnails{
    padding: 40px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.thumbnails div{
    padding: 15px 0;
}

.thumbnails img{
    max-width: 413px;
    height: 276px;
}