.layout {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

main{
    flex-grow: 1;
}

.layout .top-navbar{
	position: fixed;
    width: 100%;
	top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: whitesmoke;
    padding: 10px 10%;
    z-index: 99;
    transition: top 0.3s ease;
}

.sticky{
    top: 0 !important;
    background-color: rgba(0, 0, 0, 0.95) !important;
}

.top-navbar h1{
    text-transform: uppercase;
    font-size: 2.3rem;
    cursor: default;
}