.header {
    height: 100vh;
}
.header .bg-image::before{
    content: "";
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    -webkit-backdrop-filter: blur(3px); /* apply the blur */
    backdrop-filter: blur(3px);         /* apply the blur */
    pointer-events: none; /* make the pseudo class click-through */
    background-color: rgba(0, 0, 0, 0.25);
}

.header .bg-image{
    position: relative;
    background-image: url("http://34.65.52.225:3000/uploads/sintal-images/1463024.jpg");
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.header .bg-text{
    position: absolute;
    top: 0;
    z-index: 2;
    height: 100%;
    width: 100%;

}

.header .top-navbar{
    position: -webkit-sticky;
	position: sticky;
	top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: whitesmoke;
    padding: 10px 10%;
}

.header .top-navbar .sticky{
    position: fixed;
    top: 0;
}


.header .top-navbar h1{
    text-transform: uppercase;
    font-size: 2.3rem;
}

.header .header-text{
    margin: auto;
    padding-top: 35vh;
    color: whitesmoke;
    text-align: center;
}
.header .header-text h1{
    font-size: 2.5rem;
    padding-bottom: 10px;
}

.header .header-text h2{
    font-size: 1.5rem;
}

.header .header-text .phone-number{
    display: inline-block;
    background-color: rgb(177, 41, 7);
    border-radius: 7px;
    padding: 17px 27px;
    cursor: pointer;
}

.header .header-text .phone-number a{
    color: white;
    text-decoration: none;
}

.header .header-text a{
    font-size: 1.8rem;
}

.header .p3{
    padding: 10px;
    font-size: 1.2rem;
    margin-top: 10vh;
}

.header .scroll-down{
    display: flex;
    justify-content: center;
    padding-top: 7vh;
}